@import url(https://fonts.googleapis.com/css?family=Faster+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, right top, left top, from(rgba(7,27,82,1)), to(rgba(0,128,128,1)));
  background: -webkit-linear-gradient(right, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);
  background: linear-gradient(to left, rgba(7,27,82,1) 0%, rgba(0,128,128,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


h1 {
  font-family: 'Faster One', cursive;
  color: #0ccac4;
}
